.search{
    position: absolute;
    top: 35px;
    left: 25%;
    width: 100vh;
}
.search > h2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 559px;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 380px;
}
.search > input{
    width: 539px;
    padding: 20px;
    position: absolute;
    left: 0;
    height: 30px;
    top: 420px;
    border: none;
}
.search > input:focus{
    outline-width: 0;
}
.search > button{
    position: absolute;
    left: 0;
    top: 480px;
    text-transform: inherit;
    background-color: crimson;
    color: #fff;
    width: 579px;
}
.search > button:hover{
    background-color: #fff;
    color: crimson;
}